import longButton from '../../components/my-components/LongButton.vue'
let DataFrom={};
let cache='';
export default {
    components: {
        longButton
    },
    data() {
        return {
            val: '',
            length: 0,
            disable: true
        }
    },
    methods: {
        sure() {
            this.Cacher.setData('billId',{
                billId:cache
            })
            uni.navigateBack();
        },
        getId(val) { 
            this.disable = !val.detail.value.length;
             
            cache=val.detail.value
        }
    },
    onLoad(option){
        DataFrom=this.Cacher.getData(option.from);
        this.val=DataFrom.value.content;
        this.disable=!this.val; 
    }
}
